.button {
  display: inline-block;
  z-index: 200;
  padding: 10px 15px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  background-image: none;
  border: 3px solid #6840e5;
  border-radius: 6px;
  color: #6840e5;
  background-color: transparent;
  -webkit-transition: all 150ms ease;
  transition: all 150ms ease;
}
.button:hover {
    border: 3px solid #6840e5;
    color: #ffffff;
    background-color: #6840e5;
}
.button[disabled] {
    border: 3px solid #bbbbbb;
    background-color: #bbbbbb;
    color: #e6e6e6;
}
.button[disabled]:hover {
      border: 3px solid #bbbbbb;
      background-color: #bbbbbb;
      color: #e6e6e6;
}
.button.red {
    color: #FF5154;
    border: 3px solid #FF5154;
}
.button.red:hover {
      border: 3px solid #FF5154;
      color: #ffffff;
      background-color: #FF5154;
}
.button.full-width {
    width: 100%;
    max-width: auto;
}
.watchlist-card {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0 auto 25px auto;
  max-width: 357px;
  height: 500px;
  background-color: #f8f8f8;
  border-radius: 8px;
  padding: 20px 15px 0 15px;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-box-shadow: inset 0 150px 150px rgba(0, 0, 0, 0.3);
  -ms-box-shadow: inset 0 150px 150px rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 150px 150px rgba(0, 0, 0, 0.3);
}
.watchlist-card h2 {
    font-size: 35px;
    letter-spacing: 1px;
    margin-bottom: 0;
}
.watchlist-card h5 {
    font-size: 12px;
}
.watchlist-card h4 {
    font-size: 20px;
    color: #ffffff;
    margin: 0px 0 10px 0;
    padding-bottom: 10px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.26);
}
.watchlist-card h6 {
    z-index: 20;
    position: absolute;
    bottom: 60px;
    font-weight: bold;
    left: 0;
    width: 100%;
    padding: 10px 0 10px 15px;
    color: #ffffff;
    font-size: 16px;
    text-transform: uppercase;
    background-color: rgba(0, 0, 0, 0.2);
}
.watchlist-card h6 small {
      text-transform: none;
      color: inherit;
}
.watchlist-card h2, .watchlist-card h5 {
    z-index: 10;
    font-weight: bold;
    position: relative;
    color: #ffffff;
}
@media (max-width: 767px) {
.watchlist-card h2 {
      font-size: 25px;
}
.watchlist-card h4 {
      font-size: 15px;
}
}
.watchlist-card .more-button {
  z-index: 300;
  position: absolute;
  top: 15px;
  right: 15px;
}
.watchlist-card .slide-out-panel {
  padding-top: 50px;
  position: absolute;
  z-index: 200;
  top: 0;
  left: 0;
}
.watchlist-card .tab-panels {
  height: 360px;
}
.watchlist-card .tab-panels .button.red {
    width: 90%;
    display: block;
    margin: 25px auto 10px auto;
}
.frost-glass {
  z-index: 20;
  overflow: hidden;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 70px;
  width: 100%;
  border-top: 1px solid rgba(255, 255, 255, 0.17);
}
.frost {
  z-index: 5;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 70px;
  width: 100%;
  background-position: center 99%;
  background-size: cover;
  -webkit-transform: scale(1.08);
  transform: scale(1.08);
  -webkit-filter: blur(5px);
  filter: blur(5px);
  -webkit-box-shadow: inset 0 -50px 30px rgba(0, 0, 0, 0.2);
  -ms-box-shadow: inset 0 -50px 30px rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 -50px 30px rgba(0, 0, 0, 0.2);
}
.countdown-timer .time {
  z-index: 100;
  width: 25%;
  position: relative;
  display: inline;
  float: left;
  font-size: 24px;
  line-height: 18px;
  font-weight: 100;
  text-align: center;
  color: #ffffff;
  margin: 20px 0 0 0;
}
.countdown-timer .time i {
    font-size: 10px;
    display: block;
    width: 100%;
}
.more-button {
  z-index: 300;
  position: relative;
  overflow: hidden;
  background-image: none;
  height: 60px;
  width: 60px;
  border: none;
  border-radius: 50%;
  background-color: #ffffff;
  text-align: center;
  color: #000000;
  padding: 0;
  -webkit-transition: transform 300ms ease;
  -webkit-transition: -webkit-transform 300ms ease;
  transition: -webkit-transform 300ms ease;
  transition: transform 300ms ease;
  transition: transform 300ms ease, -webkit-transform 300ms ease;
  -webkit-transform: translate(0, 345px) scale(1);
  transform: translate(0, 345px) scale(1);
  -webkit-box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4);
  -ms-box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4);
}
.more-button span {
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}
.more-button .dripicons-plus {
    z-index: 20;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    font-size: 35px;
    line-height: 2.1;
    opacity: 1;
    -webkit-transform: translate(-60px, 0) scale(0);
    transform: translate(-60px, 0) scale(0);
}
.more-button .behind {
    z-index: 10;
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    font-size: 21px;
    line-height: 60px;
    font-weight: bold;
    opacity: 1;
    color: #6840e5;
    -webkit-transform: translate(0, 0) scale(1);
    transform: translate(0, 0) scale(1);
}
.more-button:hover, .more-button:focus {
    -webkit-box-shadow: 0 4px 25px rgba(0, 0, 0, 0.8);
    -ms-box-shadow: 0 4px 25px rgba(0, 0, 0, 0.8);
    box-shadow: 0 4px 25px rgba(0, 0, 0, 0.8);
}
.more-button:hover .behind, .more-button:focus .behind {
      opacity: 0;
      -webkit-transform: translate(60px, 0) scale(0);
      transform: translate(60px, 0) scale(0);
}
.more-button:hover .dripicons-plus, .more-button:focus .dripicons-plus {
      opacity: 1;
      -webkit-transform: translate(0, 0) scale(1);
      transform: translate(0, 0) scale(1);
}
.more-button.open {
    -webkit-transform: translate(0, 0) scale(0.5) rotate(45deg);
    transform: translate(0, 0) scale(0.5) rotate(45deg);
}
.more-button.open .behind {
      opacity: 0;
      -webkit-transform: translate(60px, 0) scale(0);
      transform: translate(60px, 0) scale(0);
}
.more-button.open .dripicons-plus {
      opacity: 1;
      -webkit-transform: translate(0, 0) scale(1);
      transform: translate(0, 0) scale(1);
}
.more-button.up-to-date .behind {
    opacity: 0;
    -webkit-transform: translate(60px, 0) scale(0);
    transform: translate(60px, 0) scale(0);
}
.more-button.up-to-date .dripicons-plus {
    opacity: 1;
    -webkit-transform: translate(0, 0) scale(1);
    transform: translate(0, 0) scale(1);
}
.slide-out-panel {
  position: absolute;
  z-index: 200;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  will-change: transform;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  -webkit-transform: translate(-100%, 0);
  transform: translate(-100%, 0);
}
.slide-out-panel.open {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
}
.tabs {
  min-height: 70px;
  width: 90%;
  margin: auto;
  overflow-x: auto;
}
.tabs .tab-button {
    width: 20%;
}
.tab-button {
  text-align: center;
  float: left;
  width: 100%;
  padding: 4px 0;
  color: #ffffff;
  display: inline-block;
  border-radius: 2px;
  background-color: transparent;
  border: 1px solid transparent;
  margin: 5px 0;
  opacity: 1;
  -webkit-transition: opacity 200ms ease;
  transition: opacity 200ms ease;
}
.tab-button.active {
    font-weight: bold;
    background-color: rgba(0, 0, 0, 0.6);
    border: 1px solid #ffffff;
}
.tab-button:hover {
    cursor: pointer;
    border: 1px solid #ffffff;
}
.tab-panels {
  position: relative;
  width: 100%;
  display: block;
  overflow-y: auto;
  height: 360px;
}
.tab-panels .tab-panel {
    z-index: 100;
    padding: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    display: block;
    -webkit-transform: translate(-100%, 0);
    transform: translate(-100%, 0);
    -webkit-transition: all 400ms ease;
    transition: all 400ms ease;
}
.tab-panels .tab-panel.active {
      z-index: 200;
      opacity: 1;
      -webkit-transform: translate(0, 0);
      transform: translate(0, 0);
}
.tab-panels::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}
.panel-row {
  margin: 10px auto;
  height: 50px;
  width: 90%;
  background-color: #ffffff;
  display: block;
  padding: 15px 10px;
  border-radius: 4px;
  -webkit-transition: opacity 500ms ease;
  transition: opacity 500ms ease;
}
.panel-row .col-35, .panel-row .col-45, .panel-row .col-20 {
    float: left;
    line-height: 1.5;
}
.panel-row .col-35 {
    width: 35%;
    position: relative;
    font-size: 12px;
    font-weight: bold;
    -webkit-transition: opacity 400ms ease;
    transition: opacity 400ms ease;
}
.panel-row .col-35 .aired-by {
      float: left;
      font-size: 11px;
      line-height: 1;
      border: 2px solid #6840e5;
      color: #6840e5;
      padding: 3px 8px;
      border-radius: 11px;
}
.panel-row .col-45 {
    width: 45%;
    font-weight: 700;
}
.panel-row .col-20 {
    width: 20%;
    text-align: right;
    font-size: 22px;
}
.panel-row .col-20 .dripicons-preview {
      cursor: pointer;
      opacity: 0.5;
      -webkit-transition: opacity 200ms ease;
      transition: opacity 200ms ease;
}
.panel-row .col-20 .dripicons-preview:hover {
        opacity: 1;
}
.panel-row .col-20 .dripicons-preview.active {
        opacity: 1;
        color: #6840e5;
}
.panel-row.disabled {
    opacity: 0.8;
}
